body{
  background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}
.jokeApp{
  width: 400px;
  min-height: 250px;
  background-color: aqua;
  border-radius: 15px;
  padding: 12px;
}

.jokeApp h1{
  border-bottom: 1px solid black;
  text-align: center;
}

.jokeApp button{
  width: 100%;
  padding: 5px;
  background-color: rgb(3, 142, 96);
  color: wheat;
  cursor: pointer;
  border-radius: 10px;
  border: none;
  
}

.jokeApp button:hover{
  background-color: antiquewhite;
}

.jokeApp p{
  padding: 7px;

}